<template>
    <div class="register-index">
        <div class="x-title">
            <div class="title">注册</div>
            <div class="text">欢迎加入左手</div>
        </div>
        <div class="box">
            <div class="item username">
                <div class="label">手机号</div>
                <div class="input">
                    <input ref="username" v-model="phone" type="tel" maxlength="13" @blur="usernameBlur" @focus="usernameFocus">
                </div>
                <div class="clear" v-if="fix.clear" @click="clearHandel">
                    <img src="../../assets/login/clear.svg">
                </div>
            </div>
            <div class="item code">
                <div class="label">验证码</div>
                <div class="input">
                    <input ref="password" v-model="input.code" type="tel" maxlength="6">
                </div>
                <div class="sms" :class="sms.active ? 'active' : ''" @click="smsHandel">{{ sms.text }}</div>
            </div>
            <div class="item password">
                <div class="label">密码</div>
                <div class="input">
                    <input ref="password" v-model="input.password" :type="view.type" @input="viewChange">
                </div>
                <div class="view" v-if="fix.view" @click="viewHandel">
                    <img src="../../assets/login/view.svg" v-if="!view.active">
                    <img src="../../assets/login/view_active.svg" v-if="view.active">
                </div>
            </div>
        </div>
        <div class="button">
            <div class="item login" @click="registerHandel">注册</div>
            <div class="item register" @click="$router.go(-1)">返回登录</div>
        </div>
    </div>
</template>

<script>
    import common from '../../utility/common';

    export default {
        data() {
            return {
                phone: '',
                input: {
                    username: '',
                    password: '',
                    code: '',
                    device: null
                },
                fix: {
                    clear: false,
                    view: false
                },
                view: {
                    type: 'password',
                    active: false
                },
                forget: {
                    active: true
                },
                sms: {
                    active: false,
                    text: '获取验证码',
                    interval: null,
                    time: 60
                }
            }
        },
        watch: {
            phone(newValue, oldValue) {
                this.phone = newValue.length > oldValue.length ? newValue.replace(/\s/g, '').replace(/(\d{3})(\d{0,4})(\d{0,4})/, '$1 $2 $3') : this.phone.trim()
                this.fix.clear = !!this.phone;
            }
        },
        mounted() {
            common.device()
                .then(response => {
                    this.input.device = response;
                });
            window.onresize = () => {
                this.forget.active = !this.forget.active;
            }
        },
        methods: {
            clearHandel() {
                this.phone = '';
                this.$refs.username.focus();
            },
            usernameFocus() {
                if (this.phone !== '') {
                    this.fix.clear = true;
                }
            },
            usernameBlur() {
                setTimeout(() => {
                    this.fix.clear = false;
                }, 10)
            },
            smsHandel() {
                let phone = this.phone.replace(/\s/ig, '');

                if (phone.length !== 11) {
                    this.$toast('请输入手机号码');
                    return;
                }

                if (this.sms.active) {
                    return;
                }

                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    mask: false,
                    message: '发送中...'
                });
                this.$axios.post('/api/app/sms/send', {
                    phone: phone
                }).then(response => {
                    let code = response.data.code;
                    if (code === 200) {
                        this.sms.active = true;
                        this.$toast('发送成功');
                        this.sms.interval = setInterval(() => {
                            this.sms.time--;
                            if (this.sms.time) {
                                this.sms.text = this.sms.time + '秒后重发';
                            } else {
                                clearInterval(this.sms.interval);
                                this.$toast.clear();
                                this.sms.active = false;
                                this.sms.text = '重发验证码';
                                this.sms.time = 60;
                            }
                        }, 1000);
                        return;
                    }
                    this.$toast('请稍后再试~');
                });
            },
            viewChange() {
                this.fix.view = !!this.input.password;
            },
            viewHandel() {
                this.view.active = !this.view.active;
                this.view.active ? this.view.type = 'text' : this.view.type = 'password';
                //this.$refs.password.focus();
            },
            registerHandel() {
                let phone = this.phone.replace(/\s/ig, '');
                let code = this.input.code;
                let password = this.input.password;

                if (phone.length !== 11) {
                    this.$toast('请输入手机号码');
                    return;
                }

                if (code.length !== 6) {
                    this.$toast('请输入验证码');
                    return;
                }

                if (password.length === 0) {
                    this.$toast('请输入密码');
                    return;
                }

                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    mask: false,
                    message: '注册中...'
                });

                this.$axios.post('/api/app/auth/register', {
                    phone: phone,
                    code: code,
                    password: password,
                    device: this.input.device,
                    platform: 'web',
                    system: navigator.userAgent,
                    version: 1000
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.$toast('注册成功,正在登录');
                            localStorage.setItem('token', result.token);
                            localStorage.setItem('salt', result.salt);
                            localStorage.setItem('expires', result.expires);
                            setTimeout(() => {
                                this.$router.replace('/user/index');
                                this.$toast.clear();
                            }, 1000);
                            return;
                        }
                        if (code === 3002) {
                            this.$toast.fail('验证码错误');
                            return;
                        }
                        if (code === 4000) {
                            this.$toast.fail('该手机号已注册，请登录');
                            return;
                        }
                        this.$toast.fail('请稍后再试~');
                    })
            }
        }
    }
</script>

